
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import MyRater from 'c%/MyRater.vue'
import MyPopupPicker from 'c%/PopupPicker.vue'
import SelectStudent from 'c%/SelectStudent.vue'
import SelectEmployee from 'c%/SelectEmployee.vue'
import SelectLesson from 'c%/SelectLesson.vue'
import CellContent from '@/views/home/comment/cell-content.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


const emptyObject = {
customer:{
name: '',
nick_name: '',
sex: 1,
birth_time: '',
school_id: 0,
school_grade: 0,
school_class: '',
first_tel: '',
first_family_rel: 0,
first_family_name: '',
second_tel: '',
second_family_rel: 0,
second_family_name: '',
from_did: 0,
intention_level: 0,
customer_status_did: 0,
bid: 0,
referer_sid: 0,
follow_eid: 0,
remark: ''
},
deputy: [],
intention: []
}

export default{
mixins: [ common,globals ],
components: {
Step,
Cell,
Group,
XInput,
MyRater,
XButton,
StepItem,
Datetime,
CellContent,
PopupPicker,
SelectLesson,
SelectStudent,
SelectEmployee,
MyPopupPicker
},
data () {
return {
step: 0,
info: util.copy(emptyObject),
sexList: [{key:1,value:'男'},{key:2,value:'女'}],
relList: [
{ key: 1,value: '自己'},
{ key: 2,value: '爸爸'},
{ key: 3,value: '妈妈'},
{ key: 4,value: '其他'}
]
}
},
methods: {
select_school (school_id) {
this.info.customer.school_id = school_id
},
addIntention () {
this.info.intention.push({
lid:0,
eid: 0
})
},
delIntention (index) {
this.info.intention.splice(index,1)
},
handleSubmit () {
this.info.customer.bid = this.bid$[0]
this.info.customer.follow_eid = this.eid$

if(/^\s*$/.test(this.info.customer.name)){
this.toast('请输入姓名','warn')
this.step = 0
return
}
if(!/^1[35789]{1}\d{9}$/.test(this.info.customer.first_tel)){
this.toast('手机号格式错误，请重新输入','warn')
this.step = 1
return
}
if(this.info.customer.from_did == 0){
this.toast('请选择招生来源','warn')
return
}
this.$rest('customers')
.post(this.info)
.success(data=>{
this.toast('添加成功！')
this.$router.push({path: '/recruit/list'})
})
},
nextStep () {
if(this.step < 2){
this.step ++
}else{
this.handleSubmit()
}
},
prevStep () {
if(this.step > 0){
this.step--
}
}
},
computed: {
btnText () {
return this.step==2?'提交':'下一步'
},
maxYear () {
return parseInt(moment(new Date()).format('YYYY'))
},
grades () {
let list = this.dicts('grade'),
result = []
if(list.length){
list.forEach(l=>{
result.push({
key: parseInt(l.name),
value: l.title
})
})
}
return result
},
schools () {
let list = this.$store.state.gvars.public_schools||[],
result = []
if(list.length){
list.forEach(l=>{
result.push({
key: l.ps_id,
value: l.school_name
})
})
}
return result
},
fromList () {
let list = this.dicts('from'),
result = []
if(list.length){
list.forEach(l=>{
result.push({
key: l.did,
value: l.title
})
})
}
return result
},
statusList () {
let list = this.dicts('customer_status'),
result = []
if(list.length){
list.forEach(l=>{
result.push({
key: l.did,
value: l.title
})
})
}
return result
}
}

}



